export default {
	auth: false,
	mounted() {


	},
	data() {
		return {
			login: {
				email: '',
				password: ''
			},
			errors: [],
			login_error: ''
		}
	},
	methods: {
	
		async userLogin() {
			await this.$auth.loginWith('laravelSanctum', { data: this.login }).then(res=>{				
				if(res.status==203){					
					this.login_error = res.data.message
				}
				this.$toaster.success("Login Successful");
			    this.$router.push('/app');
			}).catch(error=>{
				console.log(error)
			});
			
			
		}





	}
}
